/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  text-align: end;
}

.w-100 {
  width: 100%;
}

.boton-amarillo {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  font-size: 2rem;
  text-decoration: none;
  border-radius: 1%;
  padding: 1rem 3rem;
  text-align: center;
  margin: 2.5rem 2rem;
  display: inline-block;
  border: none;
  border-radius: 4%;
}
.boton-amarillo:hover {
  background-color: #cc7b08;
  cursor: pointer;
}

.boton-amarillo-block {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  font-size: 2rem;
  text-decoration: none;
  border-radius: 1%;
  padding: 1rem 3rem;
  text-align: center;
  margin: 2.5rem 2rem;
  display: block;
  border: none;
  border-radius: 4%;
}
.boton-amarillo-block:hover {
  background-color: #cc7b08;
  cursor: pointer;
}

.boton-verde {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  font-size: 2rem;
  text-decoration: none;
  border-radius: 1%;
  padding: 1rem 3rem;
  text-align: center;
  margin: 2.5rem 2rem;
  display: inline-block;
  border: none;
  margin: 2.5rem 2rem 2rem;
  border-radius: 5%;
}
.boton-verde:hover {
  background-color: #649d00;
  cursor: pointer;
}

.boton-rojo-block {
  background-color: rgb(202, 58, 58);
  color: #FFFFFF;
  font-weight: 700;
  font-size: 2rem;
  text-decoration: none;
  border-radius: 1%;
  padding: 1rem 3rem;
  text-align: center;
  margin: 2.5rem 2rem;
  display: block;
  border: none;
  width: 90%;
}
.boton-rojo-block:hover {
  background-color: #bd3333;
  cursor: pointer;
}

.boton-amarillo-block {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  font-size: 2rem;
  text-decoration: none;
  border-radius: 1%;
  padding: 1rem 3rem;
  text-align: center;
  margin: 2.5rem 2rem;
  display: block;
  border: none;
  width: 90%;
}
.boton-amarillo-block:hover {
  background-color: #cc7b08;
  cursor: pointer;
}

.dark-mode {
  background-color: #1a1a1a;
}
.dark-mode p {
  color: #FFFFFF;
}
.dark-mode a,
.dark-mode h1,
.dark-mode h2,
.dark-mode h3,
.dark-mode h4, .dark-mode td {
  color: #e1e1e1;
}
.dark-mode .card {
  background-color: #333333;
  border: 1px solid #333333;
}
.dark-mode li img {
  filter: invert(100%);
}
.dark-mode .precio {
  color: #71B100;
}
.dark-mode .formulario fieldset {
  border: 1px solid #666666;
}
.dark-mode .formulario legend,
.dark-mode .formulario label {
  color: #e1e1e1;
}
.dark-mode .formulario p {
  color: #FFFFFF;
}
.dark-mode .formulario select,
.dark-mode .formulario textarea,
.dark-mode .formulario input:not([type=submit]) {
  background-color: #333333;
  border: 1px solid #333333;
  color: #FFFFFF;
}

/*UI*/
.no-webp .inicio {
  background-image: url("../img/header.jpg");
}

.webp .inicio {
  background-image: url("../img/header.webp");
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
  background-position: center center;
  background-size: center;
}
@media (min-width: 768px) {
  .header.inicio {
    height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    height: 70rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  text-align: left;
}

.titulo-header {
  color: #FFFFFF;
  font-weight: 700;
  padding-bottom: 3rem;
  max-width: 60rem;
}

.footer {
  padding: 2.5rem;
  background-color: #333333;
  padding-top: 2.5rem;
  margin-bottom: 0;
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.copyright {
  color: #FFFFFF;
  text-align: center;
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}

.dark-mode-boton {
  width: 3rem;
  margin-top: 3rem;
  margin-bottom: 2rem;
  filter: invert(100%);
}
.dark-mode-boton:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .dark-mode-boton {
    margin-top: 0;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  padding-top: 3rem;
  align-items: center;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s, opacity 0.5s linear, height 0.5s linear;
  height: 0;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    visibility: visible;
    opacity: 1;
    height: auto;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    font-size: 2rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}

@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.icono {
  text-align: center;
}
.icono img {
  height: 10rem;
  align-items: center;
}
.icono h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .contenedor-card {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.card {
  background-color: whitesmoke;
  border: 1px solid #cccccc;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .card {
    margin-bottom: 0;
  }
}

.iconos-caracteristicas {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(3, 1fr);
  padding: 0 1rem;
}
.iconos-caracteristicas li {
  display: flex;
}
.iconos-caracteristicas li:last-of-type {
  margin-right: 0;
}
.iconos-caracteristicas li img {
  margin-right: 1rem;
  flex: 0 0.3 5rem;
}
.iconos-caracteristicas li p {
  font-size: 2rem;
  font-weight: 700;
}

.contenido-card {
  text-align: center;
  padding-top: 2rem;
}
.contenido-card h3 {
  margin: 0;
  flex-wrap: nowrap;
}

.precio {
  text-align: left;
  color: #71B100;
  font-weight: 700;
  font-size: 3rem;
  padding-left: 1rem;
}

.iconos-reducido {
  max-width: 40rem;
}

.no-webp .imagen-contacto {
  background-image: url("../img/encuentra.jpg");
}

.webp .imagen-contacto {
  background-image: url("../img/encuentra.webp");
}

.imagen-contacto {
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 4rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.imagen-contacto h2 {
  font-size: 4.2rem;
  font-weight: 700;
  color: #e1e1e1;
}
.imagen-contacto p {
  font-size: 1.8rem;
  color: #e1e1e1;
  font-weight: 600;
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.entrada-blog {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
}

.informacion-meta span {
  color: #E08709;
  font-weight: 550;
}

.texto-entrada a {
  color: #000000;
  margin-top: 2rem;
  display: block;
}
@media (min-width: 768px) {
  .texto-entrada a {
    display: inline;
  }
}
.texto-entrada h4 {
  margin: 0;
}
.texto-entrada h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71B100;
  margin-top: 1rem;
  border-radius: 5%;
}

.testimonial {
  background-color: #71B100;
  font-size: 2.4rem;
  padding: 2rem;
  color: #FFFFFF;
  border-radius: 5%;
}
.testimonial blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: -2rem;
}
.testimonial p {
  text-align: right;
  color: #FFFFFF;
}

.nosotros {
  margin-bottom: 4rem;
}

@media (min-width: 768px) {
  .nosotros-contenido {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.nosotros-texto {
  margin-top: 4rem;
}
@media (min-width: 768px) {
  .nosotros-texto {
    margin-top: 0;
  }
}
.nosotros-texto p {
  font-size: 2rem;
  text-align: center;
  margin: 0;
}
.nosotros-texto span {
  font-weight: 900;
  display: block;
  padding-bottom: 4rem;
  margin-top: 2rem;
  margin: 0;
}

.formulario {
  margin-top: 2rem;
  border-radius: 1%;
}
.formulario p {
  font-size: 1.4rem;
  color: #333333;
  margin-top: 2rem;
}
.formulario legend {
  font-size: 2rem;
}
.formulario label {
  display: block;
  text-transform: uppercase;
  font-weight: bold;
}
.formulario select,
.formulario textarea,
.formulario input:not([type=submit]) {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.formulario textarea {
  height: 15rem;
}

.forma-contacto {
  display: flex;
  max-width: 30rem;
  justify-content: space-between;
  align-items: center;
}
.forma-contacto input[type=radio] {
  margin: 0;
  width: auto;
  max-width: 5rem;
}

/* Paginas internas*/
.alerta {
  padding: 0.5rem;
  text-align: center;
  color: #FFFFFF;
  font-weight: bold;
  text-transform: uppercase;
  margin: 1rem 0;
}
.alerta.error {
  background-color: rgb(202, 58, 58);
}
.alerta.exito {
  background-color: #71B100;
}

table.propiedades {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
table.propiedades thead {
  background-color: #71B100;
}
table.propiedades thead th {
  color: #FFFFFF;
  padding: 2rem;
}
table.propiedades tbody td {
  padding: 1rem;
}
table.propiedades .imagen-tabla {
  width: 10rem;
}

.imagen-small {
  max-width: 25rem;
}

.descipcionPeque√±a {
  max-width: 25rem;
}