/** Media Queries **/
@mixin telefono {
    @media (min-width: #{$telefono}) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: #{$tablet}) {
        @content;
    }
}
  
@mixin desktop {
    @media (min-width: #{$desktop}) {
        @content;
    }
}
@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}

@mixin boton($bgColor, $display) {
    background-color: $bgColor;
    color: $blanco;
    font-weight: 700;
    font-size: 2rem;
    text-decoration: none;
    border-radius: 1%;
    padding: 1rem 3rem;
    text-align: center;
    margin: 2.5rem 2rem;
    display: $display;
    border: none;
    &:hover {
        background-color: darken($bgColor, 4);
        cursor: pointer;
    }
}